import { Component } from '@angular/core';
import { UserDataService } from '../services/user-data.service';
import { filter } from 'rxjs/operators';
import { FavoritesService } from '../services/favorites.service';
import { CartService } from '../services/cart.service';
import { ToastController } from '@ionic/angular';

@Component({
  selector: 'app-card-details',
  templateUrl: './card-details.page.html',
  styleUrls: ['./card-details.page.scss'],
})
export class CardDetailsPage {

  constructor(
    private userData: UserDataService,
    private favoritesService: FavoritesService,
    private cartService: CartService,
    private toastController: ToastController,
  ) {
    userData.subject.pipe(
      filter(e => !!e),
      filter(e => e['details'] != null)
      )
      .subscribe(
        e => {
          this.item = e.details
          this.email = e.email
        }
      )
  }
  item
  email
 
  ngOnDestroy() {
    this.userData.detailsData(null)
  }

  addToFavorites() {
    let favorites = {
      id: this.item.id,
      name: this.item.name
    };
    this.favoritesService.addToFavorites(this.email, `${this.item.id}`, favorites)
    this.presentToast(`${this.item.name} added to favorites`)
  }


  addToCart() {
    let favorites = {
      id: this.item.id,
      name: this.item.name
    };
    this.cartService.addToCart(this.email, `${this.item.id}`, favorites)
    this.presentToast(`${this.item.name} added to cart`)
  }



  async presentToast(message) {
    const toast = await this.toastController.create({
      message: message,
      position: 'top',
      duration: 2000,
      color: 'dark'
    });
    toast.present();
  }






}
